@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor_preguntas {
    margin-top: 3rem;
    flex: 1;

    @include m.tablet {
        padding: 0 3rem; 
    }

    @include m.desktop {
        margin-top: 0;
        padding: 5rem 10rem;
    }

    h1 {
        color: v.$azulOscuro;

        @include m.desktop {
            text-align: left;   
        }
    }

    
    .preguntas {
        border-bottom: 1px solid v.$grisClaro;
        padding: 1rem 0;
        
        .respuesta {
            visibility: hidden;
            color: v.$gris;
            height: 0;
            margin: 0;
            overflow: hidden;
            opacity: 0;
            
            transition-property: height opacity;
            transition-duration: 500ms;
            
            &.mostrar {
                visibility: visible;
                height: auto;
                margin: 1rem 0;
                opacity: 1;
                transition-property: height opacity;
                transition-duration: 500ms;
            }
        }
    }
    
    .pregunta {
        display: flex;
        justify-content: space-between;
        align-items: baseline;

        img {
            transition-property: transform;
            transition-duration: 500ms;
        }

        &.activo {
            font-weight: v.$bold;


            img {
                transform: rotate(180deg);

                transition-property: transform;
                transition-duration: 500ms;
            }
        }
        
        p {
            font-size: 1.3rem;
            margin: 0;
            color: v.$AzulGrisaceo;
            

            @include m.desktop {
                &:hover {
                    color: v.$rojoSuave;
                }
            }

            &:hover {
                cursor: pointer;
            }
        }
    }
}