@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor_imagen {
    @include m.tablet {
        overflow: hidden;
        display: flex;
    }
}

.imagen_header {
    margin-top: -19rem;
    flex: 1;

    @include m.desktop {
        margin: auto 0;
    }
    

    .ilustracion_header {
        width: 20rem;
        height: 20rem;
        margin: -29rem auto 0;
        background-image: url(../../build/img/illustration-woman-online-mobile.svg);
        background-position: 0 0;
        background-repeat: no-repeat;
        
        @include m.desktop {
            width: 43rem;
            height: 33rem;
            background-image: url(../../build/img/illustration-woman-online-desktop.svg);
            background-size: contain;
            margin-left: -8rem;
            margin-top: -50rem;
        }
    }

    .ilustracion_fondo {
        width: 20rem;
        height: 20rem;
        margin: 9rem auto 0;
        background-image: url(../../build/img/bg-pattern-mobile.svg);
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: contain;
        
        @include m.desktop {
            width: 70rem;
            height: 70rem;
            background-image: url(../../build/img/bg-pattern-desktop.svg);
            background-size: contain;
            margin-left: -35rem;
            margin-top: -21rem;
        }
    }

}

@include m.desktop {
    .ilustracion_caja {
        width: 16rem;
        height: 16rem;
        background-image: url(../../../build/img/illustration-box-desktop.svg);
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        top: 40%;
        left: -8.2rem;
    }   
}