@use 'base/variables' as v;
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

*, 
*::after, 
*::before {
    box-sizing: inherit;
}

body {
    min-height: 100%;
    font-size: 1.2rem;
    font-family: v.$fuente_principal;
}

.contenedor_flex {
    background: linear-gradient(to bottom, v.$violetaSuave, v.$azulSuave);
    display: flex;
    min-height: 100vh;
    align-items: center;
}

.contenedor {
    width: 90%;
    max-width: 900px;
    margin: 15rem auto 5rem;

    @include m.desktop {
        margin: 5rem auto;
    };
}

.contenedor_sm {
    background-color: v.$blanco;
    border-radius: 2.5rem;
    padding: 10rem 2.5rem 5rem 2.5rem;

    @include m.desktop {
        display: flex;  
        padding: 0;
        position: relative;
    }
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}